<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Sections</h4>
      </li>
      <li v-for="question in questions" v-bind:key="question.id" class="collection-item">
        <div class="chip">{{question.sectionId}}</div>
        {{question.title}} - {{question.review}}
        <router-link
          class="secondary-content"
          v-bind:to="{name: 'view-question', params: {question_id: question.sectionId}}"
        >
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      questions: []
    };
  },
  created() {
    db.collection("questions")
      .orderBy("sectionId")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            sectionId: doc.data().sectionId,
            title: doc.data().title,
            review: doc.data().review
          };
          this.questions.push(data);
        });
      });
  }
};
</script>


